import vue from '../../main'

/**
 * 
 * @param msg
 */
export const message = (msg) => { vue.$message(msg) }

/**
 * 
 * @param msg
 */
export const messageSuccess = (msg) => { vue.$message({message: msg, type: 'success'}) }

/**
 * 
 * @param msg
 */
export const messageWarn = (msg) => { vue.$message({message: msg, type: 'warning'}) }

/**
 * 
 * @param msg
 */
export const messageError = (msg) => { vue.$message.error(msg) }

/**
 * 
 * @param msg
 */
export const notify = (msg) => { vue.$notify.info({title: '', message: msg}) }

/**
 * 
 * @param msg
 */
export const notifySuccess = (msg) => { vue.$notify({title: '', message: msg, type: 'success'}) }

/**
 * 
 * @param msg
 */
export const notifyWarn = (msg) => { vue.$notify({title: '', message: msg, type: 'warning'}) }

/**
 * 
 * @param msg
 */
export const notifyError = (msg) => { vue.$notify.error({title: '', message: msg}) }



