import {sceneUrls} from '../api'

const scenes = [
  {id: 1, name: '', description: 'jfkaslkdsjfklasj', creator: 'admin', createtime: new Date().getTime()},
  {id: 2, name: '', description: '', creator: 'admin', createtime: new Date().getTime()},
  {id: 3, name: '', description: '', creator: 'admin', createtime: new Date().getTime()},
  {id: 4, name: '', description: '', creator: 'admin', createtime: new Date().getTime()},
  {id: 5, name: '', description: 'vv', creator: 'admin', createtime: new Date().getTime()},
  {id: 6, name: '', description: '', creator: 'admin', createtime: new Date().getTime()},
  {id: 7, name: '', description: 'v', creator: 'admin', createtime: new Date().getTime()},
  {id: 8, name: '', description: 'v', creator: 'admin', createtime: new Date().getTime()},
  {id: 9, name: '', description: '', creator: 'admin', createtime: new Date().getTime()},
  {id: 10, name: '', description: '', creator: 'admin', createtime: new Date().getTime()},
  {id: 11, name: '', description: '', creator: 'admin', createtime: new Date().getTime()},
  {id: 12, name: '', description: '', creator: 'admin', createtime: new Date().getTime()},
  {id: 13, name: '', description: '', creator: 'admin', createtime: new Date().getTime()},
  {id: 14, name: '', description: '', creator: 'admin', createtime: new Date().getTime()},
  {id: 15, name: '', description: '', creator: 'admin', createtime: new Date().getTime()},
  {id: 16, name: '', description: '', creator: 'admin', createtime: new Date().getTime()},
  {id: 17, name: '', description: '', creator: 'admin', createtime: new Date().getTime()},
  {id: 18, name: '', description: '', creator: 'admin', createtime: new Date().getTime()},
  {id: 19, name: '', description: '', creator: 'admin', createtime: new Date().getTime()}
]

export default {
  /**
   * mock bootstrap
   */
  bootstrap (mock) {
    /**
     * 
     */
    mock.onGet(sceneUrls.list).reply(config => {
      let {page, pageSize} = config
      console.log(',:{}', {page, pageSize})
      return new Promise((resolve, reject) => {
        let res = []
        let startIndex = (page - 1) * pageSize
        if (scenes.length > startIndex) {
          let endIndex = startIndex + pageSize
          res = scenes.slice(startIndex, endIndex)
        }
        resolve([200, { code: 0, msg: '', result: res }])
      })
    })

    /**
     * 
     */
    mock.onPost(sceneUrls.saveOrUpdate).reply(config => {
      let {id, name, description} = JSON.parse(config.data)
      console.log(',:{}', JSON.parse(config.data))
      return new Promise((resolve, reject) => {
        let data = {
          id: (id && id !== '' ? Number(id) : scenes.length + 1),
          name: name,
          description: description,
          creator: 'admin',
          createtime: new Date().getTime()
        }
        if (id && id !== '') {
          for (let index in scenes) {
            if (scenes[index].id === data.id) {
              scenes[index] = data
            }
          }
        } else {
          scenes.unshift(data)
        }
        resolve([200, { code: 0, msg: '', result: data }])
      })
    })

    /**
     * ,
     */
    mock.onPost(sceneUrls.flowChart).reply(config => {
      let {sceneId} = JSON.parse(config.data)
      console.log(',:{}', JSON.parse(config.data))
      return new Promise((resolve, reject) => {
        let time = new Date().getTime()
        let node1 = 'jspnode_' + time + '_1'
        let node2 = 'jspnode_' + time + '_2'
        let node3 = 'jspnode_' + time + '_3'
        let node4 = 'jspnode_' + time + '_4'
        let node5 = 'jspnode_' + time + '_5'
        let node6 = 'jspnode_' + time + '_6'
        let node7 = 'jspnode_' + time + '_7'
        let nodes = [
          {nodeCode: 'db_hive', nodeDomId: node1, name: 'hive', x: 500, y: 20, parents: [], children: [node2, node4, node5, node6, node7]},
          {nodeCode: 'fp_normalization', nodeDomId: node2, name: '', x: 200, y: 200, parents: [node1], children: [node3]},
          {nodeCode: 'ds_random', nodeDomId: node3, name: '', x: 500, y: 400, parents: [node2, node4, node5, node6, node7], children: []},
          {nodeCode: 'fp_miss_value', nodeDomId: node4, name: '', x: 300, y: 200, parents: [node1], children: [node3]},
          {nodeCode: 'fp_bucket', nodeDomId: node5, name: '', x: 500, y: 200, parents: [node1], children: [node3]},
          {nodeCode: 'fp_discretization', nodeDomId: node6, name: '', x: 700, y: 200, parents: [node1], children: [node3]},
          {nodeCode: 'fp_value_range', nodeDomId: node7, name: '', x: 800, y: 200, parents: [node1], children: [node3]}
        ]
        resolve([200, { code: 0, msg: '', result: nodes }])
      })
    })
  }
}



