const nodes = [
  {id: 1, name: '', icon: 'folder', pid: 0},
  {id: 2, name: 'hive', icon: 'database', pid: 1},
  {id: 3, name: 'mysql', icon: 'database', pid: 1},
  {id: 4, name: 'hbase', icon: 'database', pid: 1},
  {id: 5, name: '', icon: 'folder', pid: 0},
  {id: 6, name: '', icon: 'wrench', pid: 5},
  {id: 7, name: '', icon: 'wrench', pid: 5},
  {id: 8, name: '', icon: 'folder', pid: 0},
  {id: 9, name: '', icon: 'folder', pid: 8},
  {id: 11, name: '', icon: 'crosshairs', pid: 9},
  {id: 12, name: '', icon: 'crosshairs', pid: 9},
  {id: 13, name: '', icon: 'crosshairs', pid: 9},
  {id: 14, name: '', icon: 'crosshairs', pid: 9},
  {id: 15, name: '', icon: 'crosshairs', pid: 9},
  {id: 16, name: '', icon: 'folder', pid: 8},
  {id: 17, name: 'K-MEANS', icon: 'crosshairs', pid: 16},
  {id: 18, name: 'K-MEDOIDS', icon: 'crosshairs', pid: 16},
  {id: 19, name: '', icon: 'crosshairs', pid: 16},
  {id: 20, name: 'GMM', icon: 'crosshairs', pid: 16},
  {id: 21, name: '', icon: 'folder', pid: 8},
  {id: 22, name: '', icon: 'crosshairs', pid: 21},
  {id: 23, name: '', icon: 'crosshairs', pid: 21},
  {id: 24, name: '', icon: 'folder', pid: 0},
  {id: 25, name: '', icon: 'cube', pid: 24},
  {id: 26, name: '', icon: 'cube', pid: 24}
]

/**
 * nodeicon
 * @param id
 * @returns {string}
 */
function getNodeIcon (id) {
  for (let index in nodes) {
    if (nodes[index].id === id) {
      return nodes[index].icon
    }
  }
  return 'dot-circle-o'
}

export {getNodeIcon}

/**
 * 
 */
function getNodeTree () {
  let root = {
    id: 0
  }
  addChild(root)
  return root.children
}

export {getNodeTree}

/**
 * child
 * @param parent
 */
function addChild (parent) {
  let _nodes = nodes.filter(n => n.pid === parent.id)
  if (_nodes && _nodes.length > 0) {
    parent.children = []
    _nodes.forEach(n => {
      let treeNode = {
        id: n.id,
        label: n.name,
        icon: n.icon
      }
      parent.children.push(treeNode)
      addChild(treeNode)
    })
  } else {
    parent.isLeaf = true
  }
}



